<script>
  export default {
    name: 'Main',
    data: () => ({
      phoneNumber: '+6281283849479',
      phoneMessage: 'Hi Ruth, I would like to more about the Sanctuary Collection :)',
      email: 'ruthveronica_p@yahoo.com',
      emailMessage: 'Hi Ruth, I would like to more about the Sanctuary Collection :)',
      instagram: 'https://www.instagram.com/sanctuary_ruth/',
      facebook: 'https://www.facebook.com/ruthveronica.f.panggabean',
      popup: false,
      locationItems: [
        { src: require('../../public/location/location1.png') },
        { src: require('../../public/location/location2.png') },
        { src: require('../../public/location/location3.png') },
        { src: require('../../public/location/location4.png') },
        { src: require('../../public/location/location5.png') },
        { src: require('../../public/location/location6.png') },
        { src: require('../../public/location/location7.png') },
        { src: require('../../public/location/location8.png') },
        { src: require('../../public/location/location9.png') },
        { src: require('../../public/location/location10.png') },
        { src: require('../../public/location/location11.png') },
        { src: require('../../public/location/location12.png') },
        { src: require('../../public/location/location13.png') }
      ],
      projectItems: [
        { src: require('../../public/project/project1.png') },
        { src: require('../../public/project/project2.png') },
        { src: require('../../public/project/project3.png') },
        { src: require('../../public/project/project4.png') },
        { src: require('../../public/project/project5.png') },
        { src: require('../../public/project/project6.png') }
      ],
      typeItems: [
        { src: require('../../public/types/type1.png') },
        { src: require('../../public/types/type2.png') },
        { src: require('../../public/types/type3.png') },
        { src: require('../../public/types/type4.png') }
      ],
      play: false,
      songs: [
        { src: require('../../public/songs/song1.mp3'), title: 'Like a Million', singer: 'Jackboy & Kodak Black', length: 178 },
        { src: require('../../public/songs/song2.mp3'), title: 'Cold', singer: 'French Montana & Tory Lanez', length: 219 }
      ],
      activeSong: 0,
      songProgress: 0
    //
  }),
  computed: {
    audio: function() {
      return new Audio(this.songs[this.activeSong].src)
      // return
      // return new Audio(this.songs[this.songProgress].src)
    }
  },
  methods: {
    sendWhatsapp () {
      window.open(
          `https://api.whatsapp.com/send?phone=${this.phoneNumber}&text=${this.phoneMessage}`,
          "_blank"
        );
    },
    sendMail () {
      window.open(`mailto:${this.email}?subject=subject&body=${this.emailMessage}`);
    },
    openInstagram () {
      window.open(this.instagram);
    },
    openFacebook () {
      window.open(this.facebook);
    },
    previousSong () {
      this.audio.pause()
      if (this.songProgress < 10) {
        this.audio.currentTime = 0
      } else {
        if (this.activeSong === 0) this.activeSong = this.songs.length - 1
        else this.activeSong--
      }
      if (this.play) this.audio.play()
      this.songProgress = 0
    },
    nextSong () {
      this.audio.pause()
      if (this.activeSong === this.songs.length - 1) this.activeSong = 0
      else this.activeSong++
      this.songProgress = 0
      if (this.play) this.audio.play()
    },
    playPause () {
      this.play = !this.play
      if (this.play) {
        this.audio.play()
        setTimeout(this.songCounter, 1000)
      } else {
        this.audio.pause()
      }
    },
    songCounter () {
      if (!this.play) return
      const newProgress = this.songProgress + 100 / this.songs[this.activeSong].length
      if (newProgress > 100) this.songProgress = 100
      else this.songProgress  = newProgress
      setTimeout(this.songCounter, 1000)
    }
  },
  mounted () {
    // this.audio = new Audio(require(this.songs[0].src))
    // setTimeout(this.playPause, 1000)
  }
}
</script>
